<template>
  <v-row>
    <v-row>
      <template>
        <v-dialog v-model="dialog" persistent max-width="600px">
          <template v-slot:activator="{ on, attrs }">
            <v-col cols="2">
              <v-btn
                color="primary"
                dark
                v-bind="attrs"
                v-on="on"
              >
                Add filter
              </v-btn>
            </v-col>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">Adding filter</span>
            </v-card-title>

            <v-container  class="px-5">
              <v-row>
                <v-col cols="6">
                  <v-select
                    label="Select Dimension"
                    outlined
                    v-model="select"
                    :items="dimensions"
                  />
                </v-col>
                <v-col cols="6">
                  <v-select
                    label="Select action"
                    v-if="select"
                    v-model="operator"
                    outlined
                    :items="['contains', 'notContains', 'equals', 'notEquals', 'set', 'notSet']"
                  />
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    label="Value"
                    v-if="operator"
                    v-model="value"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>
    </v-row>
    <v-row v-for="(filter, index) in filters" :key="index">
      <v-alert
        dense
        color="info"
      >
        {{index}} : {{filter}}
        <v-btn @click="removeFilter(index, filter)">Remove</v-btn>
      </v-alert>
    </v-row>
  </v-row>
</template>

<script>
  export default {
    name: 'FilterComponent',
    props: ['filters', 'dimensions', 'setFilters'],
    data: () => ({
      dialog: false,
      select: '',
      operator: '',
      value: ''
    }),
    methods: {
      save() {
        this.dialog = false;
        let newFilters = [...this.filters, {member: {name: this.select}, operator: this.operator, values: [this.value]}];
        this.setFilters(newFilters);
      },
      removeFilter(index) {
        delete this.filters[index];
        let newFilters = [...this.filters];
        this.setFilters(newFilters);
      }
    }
  }
</script>

